.map-edv__wrapper {
  display: flex;
  flex-direction: column;
  padding: 3px;
  height: 80px;
  width: 360px;
  color: var(--white);
  background-color: rgba(0, 0, 0, 0.7);
  border-radius: 5px;
  overflow-x: hidden;
  background-image: url(data:image/svg+xml;base64,);
  background-repeat: no-repeat;
  background-position: 99% 2px;
}

.map-edv__content {
  display: grid;
  grid-template-columns: 1fr 30px;
  grid-template-rows: min-content min-content;
  row-gap: 3px;
  padding: 10px 30px 0 10px;

  & svg {
    grid-column: 2/3;
    grid-row: 1/-1;
  }

  & p:first-of-type {
    font-weight: 500;
    font-size: 14px;
  }

  & p:last-of-type {
    font-size: 13px;
  }
}

.map-edv__message {
  margin-top: auto;
  font-size: 11px;
  animation: run-text 10s linear infinite;
}

